:root {
  --back: #1f2937;
  --primary: #fb8122;
  --secondary: #fff;
  --highlight: #FFFFFF;
  --border-width: 2px;
  --border-radius: 4px;
  --box-shadow-width: 8px;
  --box-shadow-blur: 16px;

  /* Calculated */
  --black-or-white: #FFFFFF;
}


.black-or-white {
  color: #FFFFFF;
  color: var(--black-or-white);
}

body {
  background-color: #1f2937;
  background-color: var(--back);
  color: #fff;
  color: var(--secondary);
}

body {
  font-family: system-ui,
		-apple-system, /* Firefox supports this but not yet `system-ui` */
		'Segoe UI',
		Roboto,
		Helvetica,
		Arial,
		sans-serif,
		'Apple Color Emoji',
		'Segoe UI Emoji';
  font-weight: 300;
  /* letter-spacing: 0.1em; */
  font-size: 14px;
}

.header, .header a {
  font-family: system-ui,
		-apple-system, /* Firefox supports this but not yet `system-ui` */
		'Segoe UI',
		Roboto,
		Helvetica,
		Arial,
		sans-serif,
		'Apple Color Emoji',
		'Segoe UI Emoji';
  font-weight: 600;
  color: #FFFFFF;
  color: var(--black-or-white);
}
.header a:hover {
  color: #fb8122;
  color: var(--primary);
}

.numeric-value, .nft-card-header-item-value, .bar-graph-text-right {
  font-family: 'Fira Code';
}

/* div {
  border-radius: var(--border-radius);
} */

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

.loading {
  display: inline-block;
  width: 32px;
  height: 32px;
  font-size: 32px;
  line-height: 32px;
  -webkit-animation: rotation 2s linear infinite;
          animation: rotation 2s linear infinite;
};

.bar-graph {
  border: 1px solid #fb8122;
  border: 1px solid var(--primary);
  border-radius: 0;
}
.bar-graph-background {
  background: #fb8122;
  background: var(--primary);
  opacity: 0.5;
  border-radius: 0;
}
.bar-graph-text, .bar-graph-text-right {
  color: #FFFFFF;
  color: var(--highlight);
}

a {
  color: #fb8122;
  color: var(--primary);
}

a:hover {
  color: #FFFFFF;
  color: var(--highlight);
  cursor: hand;
}

.link:hover{
  cursor: hand;
}

.hover.link:hover{
  color: #FFFFFF;
  color: var(--highlight);
  cursor: hand;
}


