@media (min-width: 600px) {
  .panel-container {
    display: grid;
    grid-gap: 1em;
    grid-template-columns: 1fr 6fr;
  }

  .panel-project-info {
    grid-row: 1;
    grid-column: 1 / span 2;
  }

  .panel-trait-types {
    grid-row: 2;
    grid-column: 1;
  }
  
  .panel-nft-list {
    grid-row: 2;
    grid-column: 2;
  }
}

.project-list {
  flex-direction: column;
}
.project-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.project-info-image { 
  width: 110%;
  margin: auto -5%;
  overflow: hidden;
}
.project-info-title { font-size: 1.6em; }
.project-info-description { 
  overflow: hidden;
  width: -webkit-fill-available;
}
.project-info-link { word-break: break-all;}

@media (min-width: 600px) {
  .project-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(450px, 1fr));
    grid-gap: 0;
    grid-auto-flow: row dense;
  }
  .project-info {
    flex-direction: column;
  }
}

.nft-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 0;
  grid-auto-flow: row dense;
}

.nft-card .project-info {
  min-height: 240px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nft-container-single {
  margin:auto;
  max-width: 400px;
}

.nft-card {
  margin: 16px;
  /* min-height: 240px;
  width: calc(100% - 32px);
  height: calc(100% - 32px); */
  border: 2px solid #fb8122;
  border: var(--border-width) solid var(--primary);
  /* box-shadow: var(--box-shadow-width) var(--box-shadow-width) var(--box-shadow-blur) 0px var(--secondary); */
  /* box-shadow: 10px 5px 5px red; */

  background: #1f2937;

  background: var(--back);
}

.nft-card:hover {
  border: 2px solid #fff;
  border: var(--border-width) solid var(--secondary);
  box-shadow: 8px 8px 16px #fff;
  box-shadow: var(--box-shadow-width) var(--box-shadow-width) var(--box-shadow-blur) var(--secondary);
}

.hover-popup {
  border: 2px solid #fb8122;
  border: var(--border-width) solid var(--primary);
  box-shadow: 8px 8px 16px 0px #fff;
  box-shadow: var(--box-shadow-width) var(--box-shadow-width) var(--box-shadow-blur) 0px var(--secondary);
  /* box-shadow: 10px 5px 5px red; */

  background: #1f2937;

  background: var(--back);
}

.nft-card-header {
  display: grid;
  grid-template-columns: repeat(4, 1fr);

  background: #fb8122;

  background: var(--primary);
  color: #1f2937;
  color: var(--back);
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  
  text-align: center;
}

.nft-card-header-token-id {
  text-align: center;
  grid-column: span 4;
  grid-row: span 2;
}

.nft-card-header-title {
  grid-column: span 4;
  grid-row: span 2;
}

.nft-card-header-item {
  grid-column: span 2;
  grid-row: span 2;
  padding: 4px;

  display: flex;
  flex-direction: column;
}
.nft-card-header-item-2 {
  grid-column: span 2;
  grid-row: span 3;
  padding: 4px;

  display: flex;
  flex-direction: column;
}

.nft-card-header-item-label {
}
.nft-card-header-item-value {
  flex: 1 1;
  background: #1f2937;
  background: var(--back);
  color: #FFFFFF;
  color: var(--highlight);
  border-radius: 0;
  padding: 4px;
}

.nft-card-content {
  padding: 16px;
}

.nft-card-image {
  /* width: 150px; */
  height: 150px;
  margin: auto;
}
.nft-card-image img {
  /* width: 150px; */
  height: 150px;
  object-fit: contain;
}

.nft-card-stats {
  padding: 4px;
}

.nft-card-stats-row {
  display: flex;
  flex-direction: row;
}

.nft-card-stats-cell {
  flex: 1 1;
  padding: 1px;
}

.nft-card-stats-cell.link:hover {
  color: #FFFFFF;
  color: var(--highlight);
}

.nft-card-stats-title {
  color: #FFFFFF;
  color: var(--highlight);
}


.nft-trait-types-header {
  margin: 8px;
  border: 2px solid #fff;
  border: var(--border-width) solid var(--secondary);
}
.nft-trait-types-header-expandable {
  display: block;
}
.nft-trait-types-header-simple {
  display: none;
}

/* @media (min-width: 600px) {
